* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

.country-list {
    padding: 0;
}

.country-list__item {
    list-style: none;
    display: flex;
    align-items: center;
}

.country-list__name {
    margin: 2px 10px;
}

.country-info__list {
    list-style: none;
    padding: 0;
}